/** @format */

.contact {
  background-color: white;
  display: flex;
  overflow: hidden;

  .left {
    overflow: hidden;
    flex: 1;
    img {
      height: 100%;
    }
  }
  .right {
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    h2 {
      font-size: 30px;
    }
    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      input {
        width: 400px;
        height: 30px;
        font-size: 14px;
      }

      textarea {
        width: 400px;
        height: 200px;
        font-size: 14px;
      }

      button {
        width: 150px;
        height: 30px;
        color: whitesmoke;
        background-color: #2568fb;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }
      span {
        color: green;
      }
    }
  }
}
